body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-family: "PT Mono", "Georgia", serif;
  font-size: 16px;
  line-height: 22px;
  color: #666666;
  font-style: normal;
  background-color: white; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 13px 27px 14px 26px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: white;
  border: 1px solid white;
  border-radius: 5px; }

.header {
  padding: 15px 15px 70px;
  background-color: #1b2931;
  background-image: url("../img/header-background@1x.png");
  background-repeat: no-repeat;
  background-size: auto;
  background-position: 0 0;
  color: white; }
  @media (min-width: 750px) {
    .header {
      padding-right: 25px;
      padding-bottom: 163px;
      padding-left: 25px; } }
  @media (min-width: 1024px) {
    .header {
      padding-bottom: 278px; } }

.header__top {
  display: flex;
  align-items: center;
  max-width: 1410px;
  margin-right: auto;
  margin-bottom: 21px;
  margin-left: auto; }

.header__contacts {
  display: block;
  margin-right: 24px;
  color: white;
  font-size: 16px;
  line-height: 18px;
  outline: none; }
  .header__contacts span {
    display: none; }
  .header__contacts:nth-last-of-type(2) {
    margin-right: 0; }
  @media (min-width: 750px) {
    .header__contacts {
      margin-right: 34px; } }
  @media (min-width: 1024px) {
    .header__contacts {
      display: flex;
      align-items: center; }
      .header__contacts span {
        display: block;
        margin-left: 20px; }
      .header__contacts:nth-last-of-type(2) {
        margin-right: 24px; } }

.header__order {
  display: none; }
  @media (min-width: 1024px) {
    .header__order {
      display: block; } }

.logo {
  display: flex;
  margin-right: auto; }

.logo__img {
  width: 38px;
  height: 38px; }
  @media (min-width: 750px) {
    .logo__img {
      width: 52px;
      height: 52px;
      margin-right: 16px; } }

.logo__container {
  display: none; }
  @media (min-width: 750px) {
    .logo__container {
      display: block; } }

.logo__text {
  display: block; }
  .logo__text:first-child {
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-weight: 700;
    font-size: 25px;
    line-height: 29px;
    color: white; }
  .logo__text:last-child {
    font-family: "Roboto", "Arial", sans-serif;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #788891; }

.menu {
  position: relative;
  width: 37px;
  height: 30px;
  display: block;
  margin-right: 27px;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none; }
  .menu::before {
    position: absolute;
    top: 10px;
    left: 0;
    content: "";
    background-color: white;
    width: 100%;
    height: 1px;
    box-shadow: 0 10px white; }
  .menu span {
    position: absolute;
    width: 1px;
    height: 1px;
    border: 0;
    margin: -1px;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
    white-space: nowrap; }
  @media (min-width: 750px) {
    .menu {
      margin-right: 37px; } }

.promo {
  padding-top: 194px;
  background-image: url("../img/boards@1x-mobile.png");
  background-repeat: no-repeat; }
  .promo h2 {
    margin-top: 0;
    margin-bottom: 30px;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-weight: 700;
    font-size: 40px;
    line-height: 47px;
    letter-spacing: 0.07em;
    text-transform: uppercase; }
  .promo p {
    position: relative;
    margin-top: 0;
    margin-bottom: 30px;
    padding-left: 48px;
    line-height: 156%; }
    .promo p::before {
      position: absolute;
      top: 0;
      left: 0;
      content: "";
      background-color: white;
      width: 33px;
      height: 2px; }

.promo__btn-text:last-child {
  display: none; }

@media (min-width: 750px) {
  .promo__btn-text:first-child {
    display: none; }
  .promo__btn-text:last-child {
    display: block; } }
